$days: ("mon": 0, "tue": 1, "wed": 2, "thu": 3, "fri": 4, "sat": 5, "sun": 6,);
$month-length: 31;
$day-size: 100px;
$day-border: 1px solid black;
$day-font-size: 30px;
$day-color: #eee;
$day-hover-color: #FFBFCB;
$day-hover-transform: translateY(-20px);
$day-transition: all 0.5s ease;
$calendar-width: calc(7 * $day-size + 10px);
